import {
  fetchUrl,
  getToken,
  saveToken,
  redirectTo,
  formatTimestampToAMPM,
  formatDuration,
  logout,
  removeOptions,
  getAndUpdateTime,
} from "../util";
import { openModal, closeModal } from "../util/modal";

import {
  XANO_USER_SIGN_IN_REDIRECT,
  XANO_USER_PREFERENCES_REDIRECT,
  XANO_USER_DASHBOARD_REDIRECT,
  // XANO_USER_FULL_PATH_DASHBOARD_REDIRECT,
  XANO_USER_DASHBOARD_URL,
  // GOOGLE_OAUTH_USER_CONTINUE_URL,
  XANO_USER_CONFIRM_BOOKING_URL,
  XANO_USER_CANCEL_BOOKING_URL,
  XANO_USER_FILTERED_DATA_URL,
} from "../util/xano_urls";

// ? Google oauth stuff
var formHeaders = []; // ? for google (not really needed but just keep it)
var formResponse = [];
function continueOauth(code) {
  var fetchURL = new URL("https://x8ki-letl-twmt.n7.xano.io/api:ocRzyo-e/oauth/google/continue");
  fetchURL.searchParams.set("REDIRECT_URL", "https://nammax.webflow.io/user/dashboard");
  fetchURL.searchParams.set("code", code);
  fetchURL = fetchURL.toString();
  var newUrl = new URL(document.location.href);
  newUrl.searchParams.delete("code");
  newUrl.searchParams.delete("scope");
  newUrl.searchParams.delete("authuser");
  newUrl.searchParams.delete("hd");
  newUrl.searchParams.delete("prompt");
  history.replaceState(null, "", newUrl.toString());

  fetch(fetchURL, {
    headers: formHeaders,
    method: "GET",
  })
    .then((res) => res.json())
    .then((data) => (formResponse = data))
    .then(() => saveToken(formResponse.token))
    .catch((error) => {
      console.error("Error:", error);
    });
}

window.onload = function () {
  // ? for google from signin/signup page redirect
  var curUrl = new URL(document.location.href);
  var code = curUrl.searchParams.get("code");

  if (code) {
    // ? redirect was successful,
    // ? clean url and save google oauth token
    continueOauth(code);
  } else {
   const token = getToken(); // ? get token (saved from signin page)

    const logoutButton = document.querySelector("#navbar-logout-btn");
    if (logoutButton) {
      logoutButton.onclick = () => {
        logout();
      };
    }
    if (token) {
      getAndUpdateTime();
      getUserData(token);
      initFilterSessions(token);
    } else {
      // ? if there is no token
      redirectTo(`${XANO_USER_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    }
  }
};

async function getUserData(token) {
  var curUrl = new URL(document.location.href);
  var sessionFilter = curUrl.searchParams.get("session_name");
  var dateFilter = curUrl.searchParams.get("time");

  let baseUrl = XANO_USER_DASHBOARD_URL; // ? get sessions belonging to the gym that the user selected
  if ((sessionFilter && sessionFilter !== "Select Program") || dateFilter) {
    baseUrl += "?";
    if (sessionFilter) {
      baseUrl += `session_name=${sessionFilter}&`;
    }
    if (dateFilter) {
      baseUrl += `time=${dateFilter}`;
    }
    let onAFilterContainer = document.getElementById("on-a-filter-container");
    onAFilterContainer.style.display = "flex";
  }

  let data = await fetchUrl(baseUrl, "GET", false, token);

  if (data.isFetchOk !== undefined && !data.isFetchOk) {
    console.error(data.message);
    redirectTo(`${XANO_USER_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    return;
  }

  let { users, sessions } = data;

  if (users.gym_id === 0) {
    // ? if user did not select their nearest gym
    redirectTo(`${XANO_USER_PREFERENCES_REDIRECT}?reason=UNSET_DETAILS`);
  } else {
    appendUserProfileDetails(users);

    let bookedSessionsArr = [];
    let sessionsArr = [];
    sessions.forEach((session) => {
      // ? for all users within the session
      let booked = session.users.some((_user) => {
        // ? check if session is linked to authenticated user
        if (_user && users && _user.user_id === users.id) {
          // ? if it is, add to booked array
          bookedSessionsArr.push(session);
          return true;
        }
        return false;
      });

      if (!booked) {
        sessionsArr.push(session);
      }
    });

    appendBookedSessions(users, bookedSessionsArr);
    appendSessions(users, sessionsArr);
  }
}

function initFilterSessions(token) {
  let filterFormBlock = document.getElementById("filter-form-block");
  let sessionFilter = filterFormBlock.querySelector("#filter-program");
  let dateFilter = filterFormBlock.querySelector("#datepicker-input");
  $(dateFilter).attr("autocomplete", "off");

  $(dateFilter).datepicker({
    startDate: new Date(),
    format: "dd/mm/yyyy",
    language: "en",
    orientation: "bottom auto",
  });

  async function fetchFilterSelectData() {
    let data = await fetchUrl(XANO_USER_FILTERED_DATA_URL, "GET", false, token);

    if (data.isFetchOk !== undefined && !data.isFetchOk) {
      console.error(data.message);
      redirectTo(`${XANO_USER_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
      return;
    }

    const { sessions } = data;

    removeOptions(sessionFilter, 0); // ? remove all webflow skeleton options
    sessions.forEach((session_name) => {
      var opt = document.createElement("option");
      opt.value = session_name.name;
      opt.innerHTML = session_name.name;
      sessionFilter.appendChild(opt);
    });

    sessionFilter.removeEventListener("focus", fetchFilterSelectData);
  }

  // ? when the user clicks on the select dropdown, it will
  // ? get program name data
  sessionFilter.addEventListener("focus", fetchFilterSelectData);
}

function appendUserProfileDetails(user) {
  document.getElementById("user_name").innerText = user.name;
  document.getElementById("gym-location").innerText = user.location;

  if (user.profile_picture) {
    document.getElementById("profile-avatar").style.backgroundImage = `url(${user.profile_picture.url})`;
  }
}

async function appendBookedSessions(user, data) {
  const bookedContainer = document.getElementById("booked-sessions-container");
  const og_webflow_element = bookedContainer.querySelector("#booked-sessions-item");

  data.forEach((session) => {
    const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
    clonedCard.setAttribute("id", "");
    clonedCard.querySelector("#booked-session-name").innerText = session.session_name;
    clonedCard.querySelector("#booked-time").innerText = formatTimestampToAMPM(session.time, true);
    clonedCard.querySelector("#booked-duration").innerText = formatDuration(session.duration);
    clonedCard.querySelector("#booked-session-slot-bar").style.width =
      (session.users.length / session.capacity) * 100 + "%";
    clonedCard.querySelector("#booked-capacity").innerText = `${session.users.length}/${session.capacity}`;

    // ? user clicks on cancel booked session, which will then display a popup
    openModal(
      {
        baseElement: clonedCard,
        TriggerBtn: "cancel-booked-session-btn",
        _modalWindow: "cancel-booked-session-popout",
      },
      () => {
        const cancelBookedSessionPopout = document.getElementById("cancel-booked-session-popout");

        cancelBookedSessionPopout.querySelector("#session-name").innerText = session.session_name;
        cancelBookedSessionPopout.querySelector("#date").innerText = new Date(session.time).toLocaleDateString(
          "en-us",
          {
            weekday: "short",
            month: "short",
            day: "numeric",
          }
        );

        cancelBookedSessionPopout.querySelector("#time").innerText = formatTimestampToAMPM(session.time);
        cancelBookedSessionPopout.querySelector("#duration").innerText = formatDuration(session.duration);
        cancelBookedSessionPopout.querySelector("#capacity").innerHTML = `${session.users.length}/${session.capacity}`;

        // ? user confirms the cancellation of the booking
        let cancelBookingPopoutBtn = cancelBookedSessionPopout.querySelector("#confirm-cancel-booking-popout-btn");

        cancelBookingPopoutBtn.onclick = async () => {
         const token = getToken();

          const xano_input = {
            membership_end_date: user?.membership_end_date || null,
          };

          let cancelData = await fetchUrl(XANO_USER_CANCEL_BOOKING_URL + session.id, "POST", xano_input, token);

          if (cancelData.isFetchOk !== undefined && !cancelData.isFetchOk) {
            let warning_block = cancelBookedSessionPopout.querySelector("#warning_block");
            warning_block.innerText = cancelData.message;
            warning_block.style.display = "flex";
            return;
          }

          window.location.reload();
        };
      }
    );

    bookedContainer.prepend(clonedCard);
  });

  closeModal({
    TriggerBtn: "close-cancel-booked-session-modal-btn",
    _modalWindow: "cancel-booked-session-popout",
  });

  bookedContainer.classList.remove("off");
  og_webflow_element.remove();
}

async function appendSessions(user, data) {
  const sessionsContainer = document.getElementById("view-sessions-container");
  const og_webflow_element = sessionsContainer.querySelector("#view-sessions-item");

  data.forEach((session) => {
    const clonedCard = og_webflow_element.cloneNode(true);
    clonedCard.setAttribute("id", "");

    clonedCard.querySelector("#session-name").innerText = session.session_name;
    clonedCard.querySelector("#time").innerText = formatTimestampToAMPM(session.time);
    clonedCard.querySelector("#duration").innerText = formatDuration(session.duration);
    clonedCard.querySelector("#session-slot-bar").style.width = (session.users.length / session.capacity) * 100 + "%";
    clonedCard.querySelector("#capacity").innerText = `${session.users.length}/${session.capacity}`;

    openModal(
      {
        baseElement: clonedCard,
        TriggerBtn: "book-session-btn",
        _modalWindow: "book-session-popout",
      },
      () => {
        const bookSessionPopout = document.getElementById("book-session-popout");

        bookSessionPopout.querySelector("#name").innerText = session.session_name;
        bookSessionPopout.querySelector("#date").innerText = new Date(session.time).toLocaleDateString("en-us", {
          weekday: "short",
          month: "short",
          day: "numeric",
        });

        bookSessionPopout.querySelector("#time").innerText = formatTimestampToAMPM(session.time);
        bookSessionPopout.querySelector("#duration").innerText = formatDuration(session.duration);
        bookSessionPopout.querySelector("#capacity").innerText = `${session.users.length}/${session.capacity}`;
        // ? TODO: hide this if they have a membership
        // ? TODO: if they dont have any tokens, and no membership, add a warning block to contact an admin
        if (user?.membership_end_date) {
          bookSessionPopout.querySelector("#remaining_sessions").innerText =
            "Your membership ends on " + user?.membership_end_date.toString();
        } else if (user?.tokens && user?.tokens > 0) {
          bookSessionPopout.querySelector("#remaining_sessions").innerText = user?.tokens.toString().padStart(3, "0");
        } else {
          bookSessionPopout.querySelector("#remaining_sessions").innerText = "Please contact an admin for a membership";
        }

        let confirmBookingBtn = bookSessionPopout.querySelector("#confirm-booking-popout-btn");

        confirmBookingBtn.onclick = async () => {
         const token = getToken();
          const xano_input = {
            session_id: session.id,
          };
          let confirmData = await fetchUrl(XANO_USER_CONFIRM_BOOKING_URL + session.id, "POST", xano_input, token);

          if (confirmData.isFetchOk !== undefined && !confirmData.isFetchOk) {
            let warning_block = bookSessionPopout.querySelector("#warning_block");
            warning_block.innerText = confirmData.message;
            warning_block.style.display = "flex";
            return;
          }

          window.location.reload();
        };
      }
    );

    sessionsContainer.prepend(clonedCard);
  });

  closeModal({
    TriggerBtn: "close-cancel-booking-session-modal-btn",
    _modalWindow: "book-session-popout",
  });

  sessionsContainer.classList.remove("off"); // ? important, if not removed, it hides the element (we want to show it)
  og_webflow_element.remove();
}

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */
  $("form").submit(async function (e) {
    e.preventDefault();

    const $form = $(this); // The submitted form
   const token = getToken();
    if (token) {
      let filterSession = $form[0][0].value;
      let filterDate = $form[0][1].value;

      let baseUrl = XANO_USER_DASHBOARD_REDIRECT;
      if ((filterSession && filterSession !== "Select Program") || filterDate) {
        baseUrl += "?";
        if (filterSession !== "Select Program" && filterSession !== "Loading...") {
          baseUrl += `session_name=${filterSession}&`;
        }
        if (filterDate) {
          let dateSplit = filterDate.split("/");
          let d1 = new Date(+dateSplit[2], dateSplit[1] - 1, +dateSplit[0]).toISOString();
          let fixedTimezone = new Date(
            (typeof d1 === "string" ? new Date(d1) : d1).toLocaleString("en-US", {
              timeZone: "Asia/Kolkata",
            })
          );
          baseUrl += `time=${fixedTimezone.getTime() + 10000000}`;
        }
      }
      redirectTo(baseUrl);
    }
  });
});
